



* {
    box-sizing: 0;
}


body {
    margin: 0;
    background-color: #23252C;
    font-family: Inter,sans-seriff;
}

.container{ 

    margin-top: 2.5em;
    max-width: 21.875em;
    max-height: 53.125em;
    justify-content: center;
    padding: 0;
    background-color: #1A1B21;
   


}


/* header */

.user_pic{
    position: relative;
    bottom: 1.25em;
    height: 23.75em;
    width:21.875em;
    border-radius:7% 7% 0 0 ;

}


.header--user_name{
    display: flex;
    /* align-items: center; */
    justify-content:   center;

  
    padding-top: 3%;
    padding-bottom: 0;
    margin-bottom: 0.188em;
    font-weight: 1000;

    color:#FFFFFF;
    font-size: 1.563em;
}


.header--user_role{

    display: flex;
    /* align-items: center; */
    justify-content:   center;

 


    font-weight: 150;
    padding-bottom: 1.25em;

    color:#F3BF99;
    font-size: 0.938em;

}

.header--buttons{
    display: flex;
    /* align-items: center; */
    justify-content: space-evenly;
    padding-bottom: 3.125em;
}

.header--email_button, .header--linkedIn_button{
  
    display: flex;
    /* align-items: center; */
    justify-content: space-evenly;
    padding:0.313em 1.25em;


}

.header--linkedIn_button
{
    background-color:#5093E2;
    width:col-sm-6;
}

.button_mail_pic, .button_linkedin_pic{
    margin-right: 0.625em;
}

/* main */
.main{
    margin-left: 11%;
    color:#FFFFFF;
    max-width: 17em;
    padding-bottom: 20px;
}

.main--about{
    font-size: 1.125em;
    font-weight: 600;
    
}

.main--interests{
    padding-top: 0.3125em;
    font-size: 1.125em;
    font-weight: 600;
    
}

p{
    font-size: 0.71em;
    font-style: normal;
    color: #DCDCDC;
    line-height: 150%;
    opacity: 0.9;
    /* letter-spacing:0.9px; */
}

.footer{

    background-color: #161619;
    padding: 6% ;
    border-radius: 0% 0% 7% 7%;


}

.icons{
    display: flex;
    justify-content: center;
    gap:35px;
    

}
